import store from '../store'

Vue.use(VueRouter);


/**
 *       
 * @returns {function(*=, *=, *): (undefined)}
 */
const requireCheck = () => (from, to, next) => {
  // token    login  .
  if (store.getters.accessToken) {
    // console.log('<<<<<  >> to, from:', to, from)
    next()
  } else {
    console.log('<<<<<   >> to, from:', to, from)
    next('/login')
    // location.href = '/login'
  }
}

